import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UsersService {

  compListing = [{
    user : "nigga",
    experience : "nigga is unemployed"
  },{
    user : "damn",
    experience : "0 years"
  }, {
    user : "yo",
    experience: "30 yrs, old dwag"
  }, {
    user : "ewww",
    experience : "idk"
  }];

  listing = new BehaviorSubject(this.compListing);

  changeName(name){
    this.listing.next([{
      user : name,
      experience : "1"
    }])
  }
  
  updateData(){
    setTimeout(()=>{
      this.listing.next([{
        user : "helo",
        experience : "2"
      }])
    })
  }

  getData(){
    return this.listing;
  }

  constructor() { }
}
